.App {
    margin: 0 auto;
    display: grid;
    width: 1650px;
    height: 900px;
    grid-template-areas:
    'h h h'
    'n c a';
    grid-template-rows: 80px 1fr;
    grid-template-columns: 3fr 10fr 6fr;
    grid-gap: 10px;
}

.app-contents {
    grid-area: c;
    background-color: #157cb7;
    width: 100%;
    overflow-y: scroll;
}





